.LookTechnology{
  width: 99%;
  height: 98%;
  margin-top: 10px;
  display: flex;
  flex-direction: column;
  //background: white;
  overflow: auto;
  &-title{
    font-size: 16px;
    margin-top: 30px;
    font-family: PingFangSC-Semibold, PingFang SC;
    font-weight: 600;
    color: #333333;
    line-height: 22px;
  }
  &-stand{
    margin-left: 10px;
  }
  &-info{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    &Content{
      display: flex;
      //align-items: center;
      margin-top: 15px;
    }
  }
  &-input{
    width: 320px;
    height: 42px;
    //background: #FFFFFF;
    border-radius: 2px;
    display: flex;
    //align-items: center;
    margin-left: 20px;
  }
  &-card{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-top: 20px;
    background: white;
    &Title{
      font-size: 16px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #333333;
      line-height: 22px;
    }
  }
  &-table{
    margin-left: 10px;
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    &Title{
      font-size: 16px;
      font-family: PingFangSC-Semibold, PingFang SC;
      font-weight: 600;
      color: #333333;
      line-height: 22px;
    }
  }
}

@primary-color: #1890ff;