.LookTechnologyCard{
  width: 415px;
  height: 200px;
  display: flex;
  margin-top: 20px;
  background: white;
  &-card{
    width: 415px;
    height: 100%;
    display: flex;
    &Left{
      width: 134px;
      height: 100%;
      display: flex;
      flex-direction: column;
      &Title{
        margin-left: 40px;
        margin-top: 40px;
        font-size: 16px;
        font-family: SourceHanSansSC-Bold, SourceHanSansSC;
        font-weight: bold;
        color: #333333;
        line-height: 24px;
      }
      &Type{
        margin-top: 10px;
        margin-left: 30px;
        display: flex;
        justify-content: center;
        align-items: center;
        width: 84px;
        height: 33px;
        background: #E3DAD0;
        border-radius: 4px;
        &.showBgk{
          background: #EDF9E8;
          color: #4A8C1C;
        }
      }
    }
    &Right{
      width: 280px;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      overflow: auto;
      justify-content: space-between;
      &Content{
        width: 120px;
        height: 42px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-bottom: 1px solid #f0f0f0;
        cursor: pointer;
        &.showBgk{
          background: #E0E7F5;
        }
      }
    }
  }
}
@primary-color: #1890ff;